.timeline {
    position: relative;
    width: 90%;
    max-width: 1000px;
	line-height: 1.5em;
	font-size: 14px;
    margin: 30px auto;
    @include transition;

    &__content {
        width: 45%;
        background: $white;
        padding: 20px;
        box-shadow: 2px 2px 9px rgba(0, 0, 0, 0.4);
        @include transition;
        border-radius: 5px;

        .timeline__title {
            font-size:1rem;
            font-weight: 600;
            padding: 15px;
            margin: -20px -20px 0 -20px;
            background-color: $darker-cyan;
            color: $white;
            border-radius: 3px 3px 0 0;
            white-space: nowrap;
        }
        .timeline__text {
            font-size: .9rem;
            text-align: justify;
            color: $dark-black;
            padding: 0.5rem 0;
            margin-bottom: 1rem;
            line-height: 1.2;
        }
        &:before {
            content: '';
            position: absolute;
            left: 45%;
            top: 20px;
            width: 0; 
            height: 0; 
            border-top: 7px solid transparent;
            border-bottom: 7px solid transparent; 
            border-left:7px solid $darker-cyan; 
        }
    }
	&__item {
		margin-bottom: 50px;
        position: relative;
        
        &:after, 
        &:before {
            content: '';
            display: block;
            width: 100%;
            clear: both;
        }
    }
    &__icon {
        position: absolute;
        top: 0;
        left: 50%;
        width: 50px;
        height: 50px;
        background: $darker-cyan;
        margin-left: -23px;
        border-radius: 50%;
        overflow: hidden;

        img,
        svg {
            position: relative;
            top: 8px;
            left: 8px;
            width: 33px;
            height: 33px;
        }
    }
    &__btn {
        display: flex;
        justify-content: flex-end;
    }
    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        
        img {
            width: 100%;
            max-height: 350px;
            box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.5);
        }
    }
    &:before {
		content:"";
		width: 3px;
		height: 100%;
		background: $darker-cyan;
		left: 50%;
		top: 0;
		position: absolute;
	}
	&:after {
		content: "";
		clear: both;
		display: table;
		width: 100%;
    }
}

@media only screen and (min-width: 576px) {
    .timeline {
        &__content {
            .timeline__title {
                font-size: 1rem;
            }
        }
    }
}
@media only screen and (max-width: 767px) {
	.timeline {        
		&:before {
			left: 0;
		}
        &__content {
            width: 90%;
            float: right;
            
            &:before, 
            &--right:before {
                left: 10%;
                margin-left: -6px;
                border-left: 0;
                border-right: 7px solid $darker-cyan;
            }
        }
        &__icon {
            left: 0;
        }
	}
}
@media only screen and (min-width: 768px) {
    .timeline {
        &__content {
            &--right {
                float: right;
                box-shadow: -2px 2px 9px rgba(0,0,0,0.5);
    
                &:before {
                    content: '';
                    right: 45%;
                    left: inherit;
                    border-left: 0;
                    border-right: 7px solid $darker-cyan;
                }
            }
        }
    }
}