@mixin hover() {
    &:after,
    &:before {
        content: '';
        display: block;
        position: absolute;
        width: 20%;
        height: 20%;
        border: 2px solid;
        border-radius: 2px;
        @include transition;
    }
    &:before {
        top: 0;
        left: 0;
        border-bottom-color: transparent;
        border-right-color: transparent;
        border-top-color: $medium-black;
        border-left-color: $medium-black;
    }
    &:after {
        bottom: 0;
        right: 0;
        border-top-color: transparent;
        border-left-color: transparent;
        border-bottom-color: $medium-black;
        border-right-color: $medium-black;
    }
    &:hover {
        color: $darker-cyan;
        
        &:after,
        &:before {
            border-color: $darker-cyan;
            width: 100%;
            height: 100%;
        }
    }
}
@mixin transition() {
    transition: all 0.3s cubic-bezier(0.77,0.2,0.05,1.0);
}
@mixin background() {
    position: relative;
    width: 100%;
	overflow: hidden;
	background-size: cover;
	background-attachment: fixed;
}