html {
    font-family: $font-default;
    color: $white;
    scroll-behavior: smooth;
}
p, blockquote {
    color: $dark-black;
    font-size: 0.9rem;
}
.container {
    padding: 1rem 1rem;
}
.introduction,
.community {
    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,.5);
        pointer-events: none;
        z-index: 0;
    }
}
.title {
    width: 100%;
    text-align: center;
    color: $dark-black;

    &--main {
        font-size: 1.5rem;
        font-weight: 500;
        line-height: 1.2;
        letter-spacing: 1.8px;
    }
}
.logo {
    width: 100%;
    margin-top: 8px;
    padding: 0.5rem;
    color: $white;
    font-size: 2.5rem;
    font-weight: 600;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 0px;
    transform: scale(1, 1.5);
    @include transition;
}
.card {
    &__content {
        width: 100%;
        max-width: 450px;
        padding: 1rem;
        margin: 0 2rem 1rem;
        border-radius: 5px;
        background-color: $white;
        box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.4);

        &:last-child {
            margin-bottom: 2rem;
        }
    }
    &__text {
        font-size: 0.9rem;
        color: $dark-black;
        text-align: justify;

        b {
            color: $white;
            background-color: $darker-cyan;
        }
    }
    &__quotes {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 100%;
        z-index: 1;
        margin-top: 2rem;

        blockquote {
            position: relative;
            display: inline-block;
            width: 100%;
            padding: 30px 0 0 30px;
            border-radius: 10px;
            font-style: italic;
            font-size: 0.8rem;
            text-align: right;
            color: $font-color;
            
            &::before {
                content: "\"";
                position: absolute;
                display: block;
                top: 10px;
                left: 0;
                font-family: 'Bitter', serif;
                font-size: 4rem;
            }
            .author {
                display: block;
                position: relative;
                text-align: right;
                margin-top: 5px;
                color: $ocean-blue;
                padding-left: 5px;
                font-style: italic;
            }
        }
    }
}

@media only screen and (min-width: 992px) {
    .card {
        &__quotes {
            width: 50%;
        }
    }
}