.desafios {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    color: $font-color;
    
    &__day {
        margin-top: 1rem;
    }
    &--intro {
        display: flex;
        align-items: center;
        height: 100vh;
        background: url(../img/parede-amarela.webp), url(../img/parede-amarela.jpg);
        background-position: center;
        @include background;

        .title {
            font-size: 2rem;
            color: $white;
            text-shadow: 0 0 1px $black;
        }
        .img {
            margin-top: 1rem;
            box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.4);
        }
    }
    &__title {
        font-size: 1.4rem;
        font-weight: 500;
        line-height: 1.2;
        letter-spacing: 1.5px;
        margin: 0;
        color: $dark-black;
        text-align: center;
    }
    &__list,
    &__content {
        font-size: .9rem;
        color: $dark-black;
        line-height: 1.2;
    }
    &__content {
        margin: 1rem 0;
        text-align: justify;
    }
    &__list {
        list-style-type: disc;

        li {
            margin: 0 0 8px 16px;

            &:last-child {
                margin-bottom: 0;   
            }
        }
    }
    &__figure {
        margin-top: 1rem;
    }
    &__img {
        box-shadow: 0 0 5px rgba(0,0,0,0.7);
        border-radius: 5px;
    }
}

@media only screen and (min-width: 576px) {
    .desafios {
        padding: 0 1rem;

        &__day {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
        &__figure {
            width: 100%;
            margin: 1rem 0;
        }
        &__img {
            max-width: 300px;
            margin: 0 auto;
        }
    }
}

@media only screen and (min-width: 768px) {
    .desafios {
        padding: 0 2rem;

        &--intro {
            .title {
                font-size: 2.5rem;
            }
        }
        &__title {
            font-size: 2rem;
            margin-bottom: 2rem;
        }
        &__content,
        &__list {
            font-size: 1rem;
        }
        &__img {
            max-width: 450px;
        }
    }
}

@media only screen and (min-width: 992px) {
    .desafios {
        max-width: 1000px;
        margin: 0 auto;
    }
}

@media only screen and (min-width: 1200px) {
    .desafios {
        max-width: 1200px;
    }
}