.introduction {
    height: 100vh;
    background: url(../img/montanhas.webp), url(../img/montanhas.jpg);
    @include background;
    
    &__area {
        height: 100%;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .title,
        .logo {
            z-index: 3;
            text-shadow: 0px 0px 15px rgba(0, 0, 0, 0.5);
        }
        .title {
            width: 100%;
            font-size: 0.8rem;
            text-transform: uppercase;
            letter-spacing: 3px;
            color: $white;
        }
        .btn {
            z-index: 3;
            margin-top: 1rem;
        }
    }
}
@media only screen and (min-width: 768px) {
    .introduction {
        &__area {
            .title {
                font-size: 0.9rem;
            }
            .logo {
                font-size: 3rem;
                margin-top: 0;
                transform: scaleY(1.2);
            }
            .btn-hover {
                margin-top: 1.5rem;
            }
        }
    }
}