.community {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: url(../img/detox2.webp), url(../img/detox2.jpg);
    @include background;

    .title {
        color: $white;
        z-index: 1;
    }
    &__modal {
        position: fixed;
        left: -300%;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        overflow: auto;
        background-color: rgba(0,0,0,0.4);
        @include transition;

        &.active {
            left: 0;
        }
        &-content {
            position: absolute;
            top: 40%;
            left: 5%;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 90%;
            height: 130px;
            padding: 10px;
            background-color: #fefefe;
            border-radius: 10px;
            text-align: center;
        
            b {
                background: $darker-cyan;
                color: $white;
            }
        }
    }
}
.form {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 450px;
    background: $dark-black;
    border-radius: 15px;
    margin: 1rem 0;

    &__header,
    &__body {
        display: flex;
    }
    &__header {
        align-items: center;
        padding: 10px 20px;
        background: #4d4d4f;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;

        &--left {
            margin-right: auto;
        }
        &--right {
            display: flex;
        }
    }
    &__item {
        flex: 1;
        padding: 50px;

        &.left {
            display: flex;
            flex-direction: column;
        }
    }
    &__button {
        display: inline-block;
        width: 8px;
        height: 8px;
        margin-right: 3px;
        border-radius: 8px;
        background: white;

        &.close {
            background: #ed1c6f;
        }
        &.minimize {
            background-color: $green;
        }
        &.maximize {
            background: #e8e925;
        }
    }
    &__ellipsis {
        width: 3px;
        height: 3px;
        margin-left: 2px;
        border-radius: 8px;
        background: #999;
    }
    &:after {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 20px;
        right: 20px;
        bottom: 0;
        border-radius: 15px;
        box-shadow: 0 20px 40px rgba(0, 0, 0, .4);
        z-index: -1;
    }
}
.app {
    &__title {
        position: relative;
        display: flex;
        justify-content: center;
        text-align: center;
        font-size: 1.5rem;
        color: $grey;

        &:after {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            bottom: -10px;
            width: 100%;
            height: 4px;
            background: $darker-cyan;
        }
    }
    &__contact {
        margin-top: auto;
        font-size: 8px;
        color: #888;
    }
    &__form {
        &-group {
            margin-bottom: 15px;

            &.message {
                margin-top: 40px;
            }
            &.buttons {
                margin-bottom: 0;
                text-align: right;
            }
            
        }
        &-control {
            width: 100%;
            padding: 10px 0;
            background: none;
            border: none;
            border-bottom: 1px solid white;
            color: $darker-cyan;
            font-size: 14px;
            font-family: $font-default;
            outline: none;
            @include transition;

            &:focus,
            &:hover {
                border-color: $darker-cyan;
            }
            &::placeholder {
                color: $grey;
            }
            &:valid {
                border-color: $green;
            }
        }
        .btn-form {
            background-color: $green;
            width: 150px;

            &:hover,
            &:active {
                color: $green;
            }
        }
    }
}

@media only screen and (min-width: 576px) {
    .community {
        &__modal {

            &-content {
                left: 13%;
                width: 74%;
            }
        }
    }
}

@media only screen and (max-width: 767px) {
    .form {
        &__body {
            flex-direction: column;
            padding: 40px;
        }
        &__item {
            padding: 0;

            &.left {

                margin-bottom: 30px;
            }
        }
    }
} 

@media only screen and (min-width: 768px) {
    .community {
        &__modal-content {
            left: 20%;
            width: 60%;

            p {
                font-size: 1rem;
            }
        }
    }
    .form {
        max-width: 600px;

        &__item {
            padding: 40px;
        }
    }
    .app {
        &__form-control {
            font-size: .8rem;
        }
    }
}

@media only screen and (min-width: 992px) {
    .community {
        flex-direction: row;
        flex-wrap: wrap;    

        .title--main {
            width: 100%;
        }
        &__modal-content {
            left: 25%;
            width: 50%;
        }
    }
    .form {
        width: 50%;
    }
}